.block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: $cell-size;
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
}

.block--5 {
  background-color: rgb(51, 0, 0);
}

.block--6 {
  background-color: black;
}
